.slider {
    max-width: var(--related-width);
    margin: 0 auto;
}

.largeSize {
    max-width: 100%;
}

.dots {
    position: absolute;
    bottom: -60px;
    left: 0;
    display: block;
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;
}

.dots li {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;
}

.dots li button {
    font-size: 0;
    line-height: 0;
    display: block;
    width: 20px;
    height: 20px;
    padding: 5px;
    cursor: pointer;
    color: transparent;
    border: 0;
    outline: none;
    background: transparent;
}

.dots li button:before {
    font-size: 20px;
    line-height: 20px;
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    content: '•';
    text-align: center;
    opacity: .25;
    color: #fff;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.dots li button:hover:before, .dots li button:focus:before {
    opacity: 1;
}

.dots li[class="slick-active"] button:before {
    opacity: .75;
    color: #fff;
}

@media (max-width: 1154px) {
    .slider {
        margin: 0 auto;
    }

}
